<template>
    <div class="type">
        <div id="dplayer" style="width: 26rem;"></div>
    </div>
</template>
<script>
    import DPlayer from 'dplayer';
    import Hls from 'hls.js';
    export default {
        data() {
            return {

            }
        },
        mounted() {
            const dp = new DPlayer({
                container: document.getElementById('dplayer'),
                video: {
                    url: 'https://video.dious.cc/20200614/OvZleVCt/index.m3u8',
                    type: 'customHls',
                    customType: {
                        customHls: function (video, player) {
                            const hls = new Hls();
                            hls.loadSource(video.src);
                            hls.attachMedia(video);
                        },
                    },
                },
            });
        },
        methods: {

        },
    };
</script>
